html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

blockquote {
  border-left: 4px solid #ccc;
  margin-left: 10px;
  padding: 10px 20px;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

.transcluded .hide-if-transcluded {
  display: none;
}

.error-message {
  background-color: #fff5f7;
  border: 1px solid red;
  border-left-width: 4px;
  border-radius: 6px;
  padding: 10px 15px;
}

